.sol__scroll-to-top a {
  position: fixed;
  bottom: 10px;
  right: 10px;
  background-color: var(--color-secondary);
  padding: 0.75rem;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s ease-in-out;
  z-index: 1;
}

.sol__scroll-to-top-none {
  opacity: 1;
  visibility: visible;
}

.sol__scroll-to-top-none {
  opacity: 0;
  visibility: hidden;
}

.sol__scroll-to-top-arrow {
  cursor: pointer;
  color: white;
  font-size: 1.3rem;
}