.sol__blog-container_article {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  background: var(--color-blog);

  cursor: pointer;
  border-bottom: 0.5rem transparent solid;
  transition: 0.3s ease-in-out;
}

/* Hover Color */
.sol__blog-container_article:hover {
  border-color: var(--color-secondary);
  transform: scale(1.1);
}


.sol__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: black;
}

.sol__blog-container_article-image img {
  width: 100%;
  height: 100%;
}

.sol__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 1rem 1.5rem;
  height: 100%;
}

.sol__blog-container_article-content p {
  font-family: var(--font-family);
  font-size: 11.65px;
  font-weight: 700;
  line-height: 35px;

  color: var(--color-text);
}

.sol__blog-container_article-content h3 {
  font-family: var(--font-family);
  font-size: 25.11px;
  font-weight: 800;
  line-height: 30.30px;

  color: var(--color-text);
  margin-bottom: 5rem;
  cursor: pointer;
}

.sol__blog-container_article-content p:last-child {
  cursor: pointer;
}


@media screen and (max-width: 550px) {
  .sol__blog-container_article-content h3 {
    font-size: 18px;
    line-height: 25p;
  }
}