.sol__team {
  display: flex;
  flex-direction: column;

  padding: 2rem;

  /* ff 3.6+ */
  background: -moz-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* safari 5.1+,chrome 10+ */
  background: -webkit-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* opera 11.10+ */
  background: -o-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#F2FCFA', endColorstr='#D8FCF0', GradientType=0);

  /* ie 10+ */
  background: -ms-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* global 94%+ browsers support */
  background: linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);
}

.sol__team-feature {
  display: flex;
}

/* Customizing feature component as per needs */
.sol__team-feature .sol__features-container__feature {
  margin: 0;
}

/* Customizing feature component as per needs */
.sol__team-feature .sol__features-container_feature-text {
  max-width: 700px;
}

.sol__team-container {
  display: flex;
  flex-direction: row;
  margin-top: 4rem;
  justify-content: space-around;
  align-items: center;
}

.sol__team-container_member {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sol__team-container_member img {
  height: 100%;
  width: 100%;
  clip-path: circle(75px at center);
  margin-bottom: 1rem;
}

.sol__team-container_member h4 {
  color: var(--color-text);
  font-weight: 800;
  font-size: 20px;
  line-height: 25px;
  text-transform: capitalize;
}

.sol__team-container_member p {
  color: var(--color-text);
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  text-transform: capitalize;
}

.sol__team-container_member-socials {
  margin-top: 1rem;
  min-width: 150px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.sol__team-container_member-socials_icons {
  margin: 0 1rem;
  cursor: pointer;
  color: var(--color-text);
  transition: 0.1s ease-in-out;
}

.sol__team-container_member-socials_icons:hover {
  color: var(--color-secondary);
  transform: scale(1.1);
}