.sol__web3 {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.sol__web3-heading {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;

  text-align: left;
  margin-right: 5rem;
  width: 50%;
}

.sol__web3-heading h1 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  font-family: var(--font-family);
}

.sol__web3-heading img {
  padding-top: 3rem;
  width: 300px;
  transition: 0.3s ease-in-out;
}

.sol__web3-heading img:hover {
  transform: scale(1.1);
}

.sol__web3-container {
  flex: 1.5;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}


/* Media Queries */

@media screen and (max-width: 990px) {
  .sol__web3 {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .sol__web3-heading {
    margin: 0 0 2rem 0;
    width: 100%;
  }
}

@media screen and (max-width: 550px) {
  .sol__web3-heading h1 {
    font-size: 28px;
    line-height: 38px;
  }
}