.sol__footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 2rem;

  /* background: var(--color-footer); */
}

.sol__footer-heading {
  width: 100%;
  text-align: center;

  margin-bottom: 3rem;
}

.sol__footer-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 64px;
  line-height: 75px;
}

.sol__footer-btn {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 1rem;
  border: 0.15rem solid var(--color-text);
  border-radius: 100px;
  text-align: center;

  margin-bottom: 7rem;
  cursor: pointer;
}

.sol__footer-btn p {
  font-size: 18px;
  font-weight: 800;
  font-family: var(--font-family);
  line-height: 21px;
  color: var(--color-text);
  word-spacing: 2px;
}

.sol__footer-links {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 3rem;

  border-top: 0.1rem solid var(--color-secondary);
  padding-top: 3rem;
}

.sol__footer-links_logo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2rem;
}

.sol__footer-links_logo img {
  max-width: 300px;
  max-height: 150px;
}

.sol__footer-links_logo p {
  font-family: var(--font-family);
  font-size: 14px;
  line-height: 15px;
  color: var(--color-text);
}

.sol__footer-links_div {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.sol__footer-links_div h3 {
  line-height: 17px;
  font-family: var(--font-family);
  color: var(--color-secondary);

  margin-bottom: 1rem;
}

.sol__footer-links_div p {
  font-size: 15px;
  line-height: 15px;
  font-family: var(--font-family);
  color: var(--color-text);

  margin: 0.5rem 0;
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

.sol__footer-links_div p:hover {
  color: var(--color-secondary);
}

.sol__footer-copyright {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 4rem;
  text-align: center;
  width: 100%;
}

.sol__footer-copyright p {
  font-size: 12px;
  font-family: var(--font-family);
  line-height: 15px;
  color: var(--color-text);
  display: flex;
  align-items: center;
}

.sol__footer-copyright ul {
  display: flex;
  list-style-type: none;
  gap: 1rem;
  color: var(--color-text);
}

.sol__footer-copyright li {
  cursor: pointer;
}

.sol__footer-copyright li:hover {
  color: var(--color-secondary);
  transform: scale(1.1);
}

/* Media Queries */

@media screen and (max-width: 850px) {
  .sol__footer-heading h1 {
    font-size: 44px;
    line-height: 50px;
  }

  .sol__footer-copyright {
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
    gap: 1.5rem;
  }

  .sol__footer-copyright ul {
    justify-content: center;
  }

  .sol__footer-links_logo img {
    max-width: 200px;
    max-height: 100px;
  }
  
}

@media screen and (max-width: 550px) {
  .sol__footer-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }

  .sol__footer-links div {
    margin: 1rem 0;
  }

  .sol__footer-btn p {
    font-size: 14px;
    line-height: 20px;
  }

  .sol__footer-links {
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
    gap: 1rem;
  }

  .sol__footer-btn {
    margin-bottom: 4rem;
  }

  .sol__footer-copyright {
    padding-top: 1.5rem;
    display: flex;
    flex-direction: column-reverse;
    text-align: start;
    gap: 1.5rem;
  }

  .sol__footer-copyright ul {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
}

@media screen and (max-width: 400px) {
  .sol__footer-heading h1 {
    font-size: 27px;
    line-height: 38px;
  }
}