.sol__header {
  display: flex;
  flex-direction: row;
}

.sol__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  max-width: 50%;

  margin-right: 5rem;
}

.sol__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.sol__header-content p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: var(--color-text);

  margin-top: 1.5rem;
}

.sol__header-animation {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sol__header-animation video {
  width: 100%;
  height: 100%;
}


/* Media Queries */

@media screen and (max-width: 1050px) {
  .sol__header {
    flex-direction: column;
    align-items: center;
  }

  .sol__header-content {
    max-width: 100%;
    margin: 0 0 3rem;
  }

  .sol__header-video {
    max-width: 500px;
    max-height: 500px;
  }
}

@media screen and (max-width: 650px) {
  .sol__header h1 {
    font-size: 48px;
    line-height: 60px;
  }

  .sol__header p {
    font-size: 16px;
    line-height: 24px;
  }

  .sol__header-animation {
    max-width: 400px;
    max-height: 400px;
  }
}

@media screen and (max-width: 490px) {
  .sol__header h1 {
    font-size: 36px;
    line-height: 48px;
  }

  .sol__header p {
    font-size: 14px;
    line-height: 24px;
  }

  .sol__header-animation {
    max-width: 350px;
    max-height: 350px;
  }
}

