.sol__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 1.5rem 6rem;
}

.sol__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.sol__navbar-links_logo {
  margin-right: 2rem;
  display: flex;
}

.sol__navbar-links_logo img {
  width: 250px;
  height: auto;
}

.sol__navbar-links_container {
  display: flex;
  flex-direction: row;
}

.sol__navbar-socials {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.sol__navbar-socials-icons {
  margin: 0 1rem;
  cursor: pointer;
  color: var(--color-text);
  transition: 0.1s ease-in-out;
}

.sol__navbar-links_container p, 
.sol__navbar-menu_container p {
  color: var(--color-text);
  font-family: var(---font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;

  margin: 0 1rem;
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

/* Hover Start */
.sol__navbar-links_container p:hover,
.sol__navbar-menu_container p:hover {
  /* am besten als Hover Variable definieren */
  color: var(--color-secondary); 
  transform: scale(1.1);
}

.sol__navbar-socials-icons:hover {
  /* am besten als Hover Variable definieren */
  color: var(--color-secondary);
  transform: scale(1.1);
}
/* Hover Ende */

.sol__navbar-menu {
  margin-left: 1rem;

  display: none;
  position: relative;
}

.sol__navbar-menu svg {
  color: var(--color-text);
  cursor: pointer;
}

.sol__navbar-menu_container {
  display: flex;
  align-items: center;
  flex-direction: column;

  text-align: center;
  background: white;
  padding: 2rem;
  position: fixed;
  right: 0;
  left: 0;
  margin-top: 3rem;
  width: 100vw;
  z-index: 2;
}

.sol__navbar-menu_container p {
  margin: 1rem 0;
}

.sol__navbar-menu_container-socials {
  display: none;
}


/* Media Queries */

@media screen and (max-width: 1250px) {
  .sol__navbar-links_logo img {
      width: 200px;
    }
}

@media screen and (max-width: 1050px) {
  .sol__navbar-links_container {
    display: none;
  }

  .sol__navbar-links {
    flex: 0;
  }

  .sol__navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  .sol__navbar {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .sol__navbar {
    padding: 2rem;
  }

  .sol__navbar-socials {
    display: none;
  }

  .sol__navbar-menu_container-socials {
    display: block;
  }

  .sol__navbar-socials-icons {
    margin: 0 0.5rem;
  }
}