.sol__services {
  display: flex;
  flex-direction: column;

  padding: 2rem;

  /* ff 3.6+ */
  background: -moz-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* safari 5.1+,chrome 10+ */
  background: -webkit-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* opera 11.10+ */
  background: -o-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#F2FCFA', endColorstr='#D8FCF0', GradientType=0);

  /* ie 10+ */
  background: -ms-linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);

  /* global 94%+ browsers support */
  background: linear-gradient(90deg, rgba(216, 252, 240, 1) 0%, rgba(242, 252, 250, 1) 100%);
}

.sol__services-feature {
  display: flex;
}

/* Customizing feature component as per needs */
.sol__services-feature .sol__features-container__feature {
  margin: 0;
}

/* Customizing feature component as per needs */
.sol__services-feature .sol__features-container_feature-text {
  max-width: 700px;
}

.sol__services-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  margin: 4rem 0 2rem;
}

.sol__services-heading h1 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  font-family: var(--font-family);
  max-width: 510px;
}

.sol__services-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  margin-top: 2rem;
}

/* Customizing feature component as per needs */
.sol__services-container .sol__features-container__feature {
  flex: 1;
  margin: 1rem;
  min-width: 210px;
  display: unset;
  flex-direction: column;
}

/* Customizing feature component as per needs */
.sol__services-container .sol__features-container_feature-text {
  margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
  .sol__services-heading {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0rem;
  }
}

@media screen and (max-width: 650px) {

  /* Customizing feature component as per needs */
  .sol__services-feature .sol__features-container__feature {
    flex-direction: column;
  }

  /* Customizing feature component as per needs */
  .sol__services-feature .sol__features-container_feature-text {
    margin-top: 0.5rem;
  }

  .sol__services-heading h1 {
    font-size: 28px;
    line-height: 40px;
  }
}

@media screen and (max-width: 350px) {

  /* Customizing feature component as per needs */
  .sol__services-container .sol__features-container__feature {
    margin: 1rem 0;
    min-width: 100%;
  }
}